import React, { Suspense, lazy } from "react";
import "./App.css";
import Header from "./assets/header";
// import Footer from './assets/Footer';
// import ScrollToTop from "./components/ScrollToTop";
import { BrowserView, MobileView } from "react-device-detect";
// import {BrowserView,MobileView,isBrowser,isMobile} from "react-device-detect";
import { Switch, Route, Link, useLocation, Redirect } from "react-router-dom";
import Home from "./home";
import { AnimatePresence } from "framer-motion";
import PMotion from "./components/PageMotion";

const About = lazy(() => import('./pages/about'));
const Works = lazy(() => import('./pages/works/superman'));
const Contact = lazy(() => import('./pages/works/superman2'));

const NotFound = () => <div><h1>404 - Not Found!</h1><Link to="/">Go Home</Link></div>;

const App = () => {

  const locationZ = useLocation();

  return (
    <>
      <div className="n--se"></div>
      <Header />
      {/* <ScrollToTop/> */}
      <BrowserView>
        <AnimatePresence exitBeforeEnter initial={false}>
          <Switch location={locationZ} key={locationZ.pathname}>
            <Route exact path="/">
              <Page1 />
            </Route>
            <Route path="/pages/about">
              <Page2 />
            </Route>
            <Route path="/pages/works/superman">
              <Page3 />
            </Route>
            <Route path="/pages/works/superman2">
              <Page4 />
            </Route>
            <Route path='*'>
              <NotFound />
              <Redirect to="/" />
            </Route>
          </Switch>
        </AnimatePresence>
      </BrowserView>
      <MobileView>
        <AnimatePresence exitBeforeEnter initial={false}>
          <Switch location={locationZ} key={locationZ.pathname}>
            <Route exact path="/">
              <Page1 />
            </Route>
            <Route path="/pages/about">
              <Page2 />
            </Route>
            <Route path="/pages/works/superman">
              <Page3 />
            </Route>
            <Route path="/pages/works/superman2">
              <Page4 />
            </Route>
          </Switch>
        </AnimatePresence>
      </MobileView>
      {/* <Footer/> */}
    </>
  );
}

const Page1 = () => {
  return (
    <PMotion>
      <Link to="/"/>
      <Home />
    </PMotion>
  );
}
const Page2 = () => {
  return (
    <PMotion>
      <Link to="/pages/about"/>
      <Suspense fallback={<div className="loading">Loading...</div>}>
        <About />
      </Suspense>
    </PMotion>
  )
}
const Page3 = () => {
  return (
    <PMotion>
      <Link to="/pages/works/superman"/>
      <Suspense fallback={<div className="loading">Loading...</div>}>
        <Works />
      </Suspense>
    </PMotion>
  )
}
const Page4 = () => {
  return (
    <PMotion>
      <Link to="/pages/works/superman2"/>
      <Suspense fallback={<div className="loading">Loading...</div>}>
        <Contact />
      </Suspense>
    </PMotion>
  )
}

export default App;