const RandomImage = () => {

    const rI = Math.floor(Math.random() * quotes.length);
    const selectQ = quotes[rI];

    return (
        <>
            <img src={selectQ} alt="quote" />
            {/* <button onClick={btnselectQ}>awd</button> */}
        </>
    );
}

export default RandomImage;