import React, { useRef } from 'react';
import { isFirefox } from 'react-device-detect'
import { LocomotiveScrollProvider } from 'react-locomotive-scroll';

export const matchFirefox = (multiplier = 1, level = 2.5) => Math.round((isFirefox ? level * multiplier : multiplier) * 100) / 100

const LScroll = ({ children }) => {
    const containerRef = useRef(null);

    return (
        <LocomotiveScrollProvider
            options={
                {
                    smooth: true,
                    lerp: 0.06,
                    touchMultiplier: 2,
                    multiplier: matchFirefox(1),
                    direction: 'vertical',
                    reloadOnContextChange: true,
                    initPosition: {
                        x: 0,
                        y: 0
                    },
                    tablet: {
                        smooth: false,
                        direction: 'vertical',
                        gestureDirection: 'vertical',
                        breakpoint: 1024,
                    },
                    smartphone: {
                        smooth: false,
                        direction: 'vertical',
                        gestureDirection: 'vertical'
                    }
                }
            }
            watch={
                [
                    //...all the dependencies you want to watch to update the scroll

                ]
            }
            containerRef={containerRef}>
            <div data-scroll-container ref={containerRef}>
                <div id="scrl-sec" data-scroll-section>
                    {children}
                </div>
            </div>           
            {/* <div data-scroll-container ref={containerRef} >
                {children}
            </div> */}
        </LocomotiveScrollProvider>
    )
};

export default LScroll;