import React from "react";
import { motion } from "framer-motion";
import { BrowserView, MobileView } from "react-device-detect";

const PageMotion = ({ children }) => {

    return (
        <>
            <BrowserView>
                <main className="m-page-cont">
                    <div>
                        <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .9 }} exit={false} style={{ width: '120%', height: '30%', zIndex: 7, borderRadius: '0 3vw 3vw 0 ', top: 0, background: '#D9544B' }} />
                        <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .9 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '30%', zIndex: 7, borderRadius: '3vw 0 0 3vw', top: 0, background: '#D9544B' }} />
                        <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .9, delay: .1 }} exit={false} style={{ width: '120%', height: '35%', zIndex: 7, background: '#f6c04b', borderRadius: '0 3vw 3vw 0 ', top: '20%' }} />
                        <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .9, delay: .2 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '35%', zIndex: 7, background: '#f6c04b', borderRadius: '3vw 0 0 3vw', top: '20%' }} />
                        <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .9, delay: .2 }} exit={false} style={{ width: '120%', height: '20%', zIndex: 7, background: '#4E9CB0', borderRadius: '0 3vw 3vw 0 ', top: '45%' }} />
                        <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .9, delay: .3 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '20%', zIndex: 7, background: '#4E9CB0', borderRadius: '3vw 0 0 3vw', top: '45%' }} />
                        <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .9, delay: 0 }} exit={false} style={{ width: '120%', height: '15%', zIndex: 8, background: '#333333', borderRadius: '0 3vw 3vw 0 ', top: '64%' }} />
                        <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .9, delay: .1 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '15%', zIndex: 8, background: '#333333', borderRadius: '3vw 0 0 3vw', top: '64%' }} />
                        <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .9, delay: .07 }} exit={false} style={{ width: '120%', height: '25%', zIndex: 7, background: '#A9C368', borderRadius: '0 3vw 3vw 0 ', top: '75%' }} />
                        <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .9, delay: .2 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '25%', zIndex: 7, background: '#A9C368', borderRadius: '3vw 0 0 3vw', top: '75%' }} />
                    </div>
                    {children}
                </main>
            </BrowserView>
            <MobileView>
                <main className="m-page-cont">
                    <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .6 }} exit={false} style={{ width: '120%', height: '30%', zIndex: 7, borderRadius: '0 50px 50px 0 ', top: 0, background: '#D9544B' }} />
                    <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .6 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '30%', zIndex: 7, borderRadius: '50px 0 0 50px', top: 0, background: '#D9544B' }} />
                    <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .6, delay: .1 }} exit={false} style={{ width: '120%', height: '30%', zIndex: 7, background: '#f6c04b', borderRadius: '0 50px 50px 0 ', top: '20%' }} />
                    <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .6, delay: .2 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '30%', zIndex: 7, background: '#f6c04b', borderRadius: '50px 0 0 50px', top: '20%' }} />
                    <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .6, delay: .2 }} exit={false} style={{ width: '120%', height: '20%', zIndex: 7, background: '#4E9CB0', borderRadius: '0 50px 50px 0 ', top: '39%' }} />
                    <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .6, delay: .3 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '20%', zIndex: 7, background: '#4E9CB0', borderRadius: '50px 0 0 50px', top: '39%' }} />
                    <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .6, delay: 0 }} exit={false} style={{ width: '120%', height: '15%', zIndex: 8, background: '#333333', borderRadius: '0 50px 50px 0 ', top: '54%' }} />
                    <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .6, delay: .1 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '15%', zIndex: 8, background: '#333333', borderRadius: '50px 0 0 50px', top: '54%' }} />
                    <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .6, delay: .07 }} exit={false} style={{ width: '120%', height: '20%', zIndex: 7, background: '#A9C368', borderRadius: '0 50px 50px 0 ', top: '67%' }} />
                    <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .6, delay: .2 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '20%', zIndex: 7, background: '#A9C368', borderRadius: '50px 0 0 50px', top: '67%' }} />
                    <motion.span className="m-page" initial={{ x: '0' }} animate={{ x: '-101%' }} transition={{ duration: .6, delay: .07 }} exit={false} style={{ width: '120%', height: '15%', zIndex: 7, background: '#26A397', borderRadius: '0 50px 50px 0 ', top: '85%' }} />
                    <motion.span className="m-page" initial={false} animate={{ x: '100%' }} transition={{ duration: .6, delay: .2 }} exit={{ x: '-10%' }} style={{ width: '120%', height: '15%', zIndex: 7, background: '#26A397', borderRadius: '50px 0 0 50px', top: '85%' }} />                   
                    {children}
                </main>
            </MobileView>
        </>
    );
}

export default PageMotion;