import React from "react";
import { Scene, Controller } from "react-scrollmagic";
import { Tween } from "react-gsap";
import RandomImage from "../components/RandomImgAndColor/RandomImage";
import styled from 'styled-components';
import { motion } from "framer-motion";
import { BrowserView, MobileView } from "react-device-detect";

const SectionWipes2Styled = styled.div`
    .st0, .st01, .st02 {fill:#D9544B;}
    .st01{fill:#D9544B}
    .st02{fill:#D9544B;}
    .st1{fill:#f6c04b; opacity: .9;}
    .st2{fill:#26A397;}
    .st3, .st31{fill:#A9C368; opacity: .9;}
    .st4{fill:#333333;opacity: .95;}
    .st5, .st51{fill:#4E9CB0;opacity: .95;}

    .act-h .st0, .act-h .st01, .act-h .st02{animation:8s animTr1 infinite ease .6s;}
    .act-h .st01{animation:8s animTr1 infinite ease .97s;}
    .act-h .st02{animation:8s animTr1 infinite ease 1.3s;}
    .act-h .st1{ animation:8s animTr1 infinite ease; }
    .act-h .st2{animation:8s animTr2 infinite ease .2s;}
    .act-h .st3, .act-h .st31{animation:8s animTr2 infinite ease 1s;}
    .act-h .st31{animation:8s animTr2 infinite ease 1.5s;}
    .act-h .st4{ animation:7s animTr1 infinite ease 2s;}
    .act-h .st5, .act-h .st51{animation:7s animTr1 infinite ease .5s;}
    .act-h .st51{animation:7s animTr1 infinite ease .77s;}

    @keyframes animTr1 {
        0% {transform: translate3d(0,0,0);}
        50% {transform: translate3d(0,15%,0);}
        100% {transform: translate3d(0,0,0);}
      }
      @keyframes animTr2 {
        0% {transform: translate3d(0,0,0);}
        50% {transform: translate3d(0,10%,0);}
        100% {transform: translate3d(0,0,0);}
      } 
`;

export default function Welcome() {

    return (
        <>
            <SectionWipes2Styled>
                <section className="h--sec">
                    <div className="sc-d">
                        <svg viewBox="0 0 90 90"><path d="M34.7,67.5c-11.3,0-20.5-9.2-20.5-20.5V22c0-11.3,9.2-20.5,20.5-20.5S55.3,10.7,55.3,22v25C55.3,58.3,46.1,67.5,34.7,67.5z" /><circle className="sr-d" cx="34.3" cy="21.6" r="5" /></svg>
                    </div>
                    <Controller>
                        <div className="h--quote-cont">
                            <motion.div initial={{ opacity: 0, x: "15%" }} animate={{ opacity: 1, x: "0" }} transition={{ ease: "easeOut", duration: 1.2, delay: 1 }} >
                                <BrowserView>
                                    <Scene duration="50%" triggerHook="onLeave">
                                        <Tween from={{ yPercent: 0 }} to={{ yPercent: -30 }} >
                                            <div className="h--quote prl-a-i">
                                                <RandomImage />
                                            </div>
                                        </Tween>
                                    </Scene>
                                </BrowserView>
                                <MobileView>
                                    <div className="h--quote">
                                        <RandomImage />
                                    </div>
                                </MobileView>
                            </motion.div>
                        </div>
                        <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ duration: 1, delay: 1.3 }} >
                            <Scene duration="110%" triggerHook="onLeave" classToggle="act-h">
                                <Tween from={{ yPercent: 0 }} to={{ yPercent: -15}}>
                                    <div className="tr--1 prl-a-i">
                                        {/* <svg viewBox="0 0 990 977" ><rect x="723" y="450" className="st0" width="268" height="1095.3" /><rect x="723" y="332" className="st01" width="268" height="60" /><rect x="879" y="215" className="st02" width="156.9" height="60" /><rect x="490" y="580" className="st1" width="307.5" height="973.1" /><rect x="0" y="900" className="st2" width="161.6" height="463.8" /><rect x="73" y="800" className="st3" width="222.8" height="568.9" /><rect x="73" y="700" className="st31" width="131.7" height="55" /><rect x="245" y="650" className="st4" width="172.1" height="1200" /><rect x="350" y="900" className="st5" height="695.7" width="229.7" /><rect x="350" y="793" className="st52" height="51" width="229.7" /></svg> */}
                                        <svg viewBox="0 0 991 1300"><path className="st0" d="M722.9,1616V407.5c0-33,26.8-59.8,59.8-59.8h148.4c33,0,59.8,26.8,59.8,59.8V1616H722.9z" /><path className="st01" d="M962,266.9L962,266.9c0-18.5-15-33.5-33.5-33.5H815c-18.5,0-33.5,15-33.5,33.5v0c0,18.5,15,33.5,33.5,33.5h113.5C947,300.5,962,285.5,962,266.9z" /><path className="st02" d="M956,158.9L956,158.9c0-18.5-15-33.5-33.5-33.5H890c-18.5,0-33.5,15-33.5,33.5v0c0,18.5,15,33.5,33.5,33.5h32.5C941,192.5,956,177.5,956,158.9z" /><path className="st1" d="M494.1,1616V552.8c0-33,26.8-59.8,59.8-59.8h187.8c33,0,59.8,26.8,59.8,59.8V1616H494.1z" /><path className="st2" d="M0.6,1616V986.1c0-33,26.8-59.8,59.8-59.8h41.9c33,0,59.8,26.8,59.8,59.8V1616H0.6z" /><path className="st3" d="M296.3,1616V933.9c0-33-26.8-59.8-59.8-59.8H133.3c-33,0-59.8,26.8-59.8,59.8V1616H296.3z" /><path className="st31" d="M228.2,796.9L228.2,796.9c0-18.5-15-33.5-33.5-33.5H130c-18.5,0-33.5,15-33.5,33.5v0c0,18.5,15,33.5,33.5,33.5h64.6C213.1,830.5,228.2,815.5,228.2,796.9z" /><path className="st4" d="M243.1,1616V628.8c0-33,26.8-59.8,59.8-59.8h52.4c33,0,59.8,26.8,59.8,59.8V1616H243.1z" /><path className="st5" d="M356,1616V807.1c0-33,26.8-59.8,59.8-59.8h110.1c33,0,59.8,26.8,59.8,59.8V1616H356z" /><path className="st51" d="M561,670.9L561,670.9c0-18.5-15-33.5-33.5-33.5H414c-18.5,0-33.5,15-33.5,33.5v0c0,18.5,15,33.5,33.5,33.5h113.5C546,704.5,561,689.5,561,670.9z" /></svg>
                                    </div>
                                </Tween>
                            </Scene>
                        </motion.div>
                    </Controller>
                </section>
            </SectionWipes2Styled>
        </>
    );
};
