import React from "react";

export default function footer(){

    const StyleFooter = {
        padding: '40px 0',
        position: 'relative',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        textAlign: 'center'

    }

    return (
        <footer>
            <div style={StyleFooter}>
                <p>2022 © Ivanwebstudio<br/>All rights reserved</p>
            </div>
        </footer>
    )
}