//-------> OldSchoolMenuLink react-router //////////////////////////

function OldSchoolMenuLink({ label, to, activeOnlyWhenExact }) {
  let match = useRouteMatch({
    path: to,
    exact: activeOnlyWhenExact
  });

  return (
    <Link className={match ? "n-active" : ""} to={to}>{label}</Link>
  );
}

