//-------> navigation //////////////////////////

const toggleClass = () => {

  const nMCont = document.querySelector('#n-m-cont');
  const nav = document.querySelector('.navigation ul');
  const logo = document.querySelector('.l--go');

  // let btnToggle = nMCont.className;
  // let newClassName = btnToggle === 'act' ? '' : 'act'
  // nMCont.className = newClassName;

  nMCont.classList.toggle('act');
  logo.classList.toggle('c-color');

  const wArr = [nav, logo];

  wArr.map(ir => ir.onclick = () => {
    setTimeout(() => {
      nMCont.classList.add('no-act');
      nMCont.classList.remove('act');
      logo.classList.remove('c-color');
    }, 1000);
    setTimeout(() => {
      nMCont.classList.remove('no-act');
    }, 1700);
  });
};

